export interface ProposalInfo {
  id: ethers.BigNumberish;
  title: string;
  description: string;
  startTime: ethers.BigNumberish;
  endTime: ethers.BigNumberish;
  isActive: boolean;
  totalVotes: ethers.BigNumberish;
  optionAVotes: ethers.BigNumberish;
  optionBVotes: ethers.BigNumberish;
}

export class SimpleContractService {
  private contract: ethers.Contract;
  private provider: ethers.JsonRpcApiProvider;
  private signer: ethers.JsonRpcSigner;

  constructor(provider: ethers.JsonRpcApiProvider, signer: ethers.JsonRpcSigner) {
    this.provider = provider;
    this.signer = signer;
    this.contract = new ethers.Contract(CONTRACTS.SIMPLE_VOTING, SIMPLE_VOTING_ABI, signer);
  }

  // Get contract instance
  getContractInstance(): ethers.Contract {
    return this.contract;
  }

  // Get proposal count
  async getProposalCount(): Promise<number> {
    try {
      const count = await this.contract.proposalCount();
      return Number(count);
    } catch (error) {
      console.error("Error getting proposal count:", error);
      throw error;
    }
  }

  // Get all proposals
  async getAllProposals(): Promise<ProposalInfo[]> {
    try {
      const proposals = await this.contract.getAllProposals();
      return proposals;
    } catch (error) {
      console.error("Error getting all proposals:", error);
      throw error;
    }
  }

  // Get proposal info by ID
  async getProposalInfo(proposalId: number): Promise<ProposalInfo> {
    try {
      const proposal = await this.contract.getProposalInfo(proposalId);
      return proposal;
    } catch (error) {
      console.error("Error getting proposal info:", error);
      throw error;
    }
  }

  // Check if user has voted on a proposal
  async hasVoted(proposalId: number, userAddress: string): Promise<boolean> {
    try {
      const hasVoted = await this.contract.hasVoted(proposalId, userAddress);
      return hasVoted;
    } catch (error) {
      console.error("Error checking if user has voted:", error);
      throw error;
    }
  }

  // Get user's vote option for a proposal
  async getVoteOption(proposalId: number, userAddress: string): Promise<number> {
    try {
      const voteOption = await this.contract.getVoteOption(proposalId, userAddress);
      return Number(voteOption);
    } catch (error) {
      console.error("Error getting vote option:", error);
      throw error;
    }
  }

  // Check if proposal is active
  async isProposalActive(proposalId: number): Promise<boolean> {
    try {
      const isActive = await this.contract.isProposalActive(proposalId);
      return isActive;
    } catch (error) {
      console.error("Error checking if proposal is active:", error);
      throw error;
    }
  }

  // Create a new proposal
  async createProposal(
    title: string,
    description: string,
    startTime: number,
    endTime: number,
  ): Promise<ethers.ContractTransactionResponse> {
    try {
      const tx = await this.contract.createProposal(title, description, startTime, endTime);
      return tx;
    } catch (error) {
      console.error("Error creating proposal:", error);
      throw error;
    }
  }

  // Cast a vote
  async vote(proposalId: number, option: number): Promise<ethers.ContractTransactionResponse> {
    try {
      // Validate option
      if (option !== 1 && option !== 2) {
        throw new Error("Invalid option. Must be 1 (Option A) or 2 (Option B)");
      }

      const tx = await this.contract.vote(proposalId, option);
      return tx;
    } catch (error) {
      console.error("Error casting vote:", error);
      throw error;
    }
  }

  // End a proposal
  async endProposal(proposalId: number): Promise<ethers.ContractTransactionResponse> {
    try {
      const tx = await this.contract.endProposal(proposalId);
      return tx;
    } catch (error) {
      console.error("Error ending proposal:", error);
      throw error;
    }
  }

  // Listen to proposal creation events
  onProposalCreated(callback: (proposalId: number, title: string, startTime: number, endTime: number) => void) {
    this.contract.on("ProposalCreated", (proposalId, title, startTime, endTime) => {
      callback(Number(proposalId), title, Number(startTime), Number(endTime));
    });
  }

  // Listen to vote cast events
  onVoteCast(callback: (proposalId: number, voter: string, option: number, timestamp: number) => void) {
    this.contract.on("VoteCast", (proposalId, voter, option, timestamp) => {
      callback(Number(proposalId), voter, Number(option), Number(timestamp));
    });
  }

  // Remove all event listeners
  removeAllListeners() {
    this.contract.removeAllListeners();
  }
}
